import { useEffect, useState } from "react";
import {loadUserData} from './loadData'


const Login=(prop)=>{
    const [lg,setlg]=useState(true);
    const [username,setUsername]=useState('');
    const [password,setPassword]=useState('');
    const [error,setError]=useState('');
    const sendsignup=async (username,password)=>{
        const xhr = new XMLHttpRequest();
        xhr.open('POST','/signup');
        xhr.setRequestHeader('Content-Type','application/json');
        xhr.send(JSON.stringify({
            username,password
        }));
        xhr.onload=()=>{
            let res=JSON.parse(xhr.responseText);
            if(res.token){
                prop.onLogin(username);
            }else{
                setError(res.error);
            }
        }
    }

    useEffect(()=>{

    },[]);

    const sendlogin=(username,password)=>{
        const xhr = new XMLHttpRequest();
        xhr.open('POST','/login');
        xhr.setRequestHeader('Content-Type','application/json');
        xhr.send(JSON.stringify({
            username,password
        }));
        xhr.onload=()=>{
            let res=JSON.parse(xhr.responseText);
            if(res.token){
                prop.onLogin(username);
            }else{
                setError(res.error);
            }
        }
    }
    if(lg){
        return(
            <div className="login_container">
                <div className="wrap1">
                <h1>Login</h1>
                <input  className="input1"
                        type="username" 
                        id="username" 
                        placeholder="Username"
                        onChange={(e)=>{setUsername(e.target.value)}}
                />
                <input  className="input1" 
                        type="password"
                        id="password"
                        placeholder="Password"
                        onChange={(e)=>{setPassword(e.target.value)}}
                />
                <span className="error">{error}</span>
                <div className="btn1"
                                onClick={()=>{sendlogin(username,password)}}
                                >Login</div>
                <span className="link"
                                onClick={()=>{lg?setlg(false):setlg(true);}}
                                >Dont have anaccount?</span>
                </div>

            </div>
            );
    }else{
        return(
            <div className="login_container">
                <div className="wrap1">
                <h1>Signup</h1>
                <input  className="input1"
                        type="username" 
                        id="username" 
                        placeholder="Username"
                        onChange={(e)=>{setUsername(e.target.value)}}
                />
                <input  className="input1" 
                        type="password"
                        id="password"
                        placeholder="Password"
                        onChange={(e)=>{setPassword(e.target.value)}}
                />
                <span className="error">{error}</span>
                <div className="btn1" onClick={()=>{sendsignup(username,password)}}>Signup</div>
                <span className="link"onClick={()=>{lg?setlg(false):setlg(true);}}
                >Already have anaccount?</span>
                </div>
            </div>
            );
    }
    
}


export default Login;
