import { useEffect, useState } from "react";
const ContactList=(prop)=>{
    const[search,setSearch]=useState(false);
    const[contacts,setContacts]=useState([]);

    const handleSearch=(e)=>{
        if(e.target.value.length>2){
            let xhr=new XMLHttpRequest();
            xhr.open('GET',`/search?q=${e.target.value}`);
            xhr.send();
            xhr.onload=()=>{
                setSearch(JSON.parse(xhr.responseText));
            }
        }else{
            setSearch(false);
        }
    }
    
    const handleAddContact=(contact)=>{
        let xhr=new XMLHttpRequest();
            xhr.open('POST',`/addContact`);
            xhr.setRequestHeader('Content-Type','application/json');
            xhr.send(JSON.stringify({
                contact
            }));
            xhr.onload=()=>{
            }
    }
    useEffect(()=>{
        if(!search){
            let xhr=new XMLHttpRequest();
            xhr.open('GET',`/getContacts`);
            xhr.send();
            xhr.onload=()=>{
                setContacts(JSON.parse(xhr.responseText));
            }
        } 
    },[search]);

    return(
        <div className="contactContainer">
        <div className="nav1">
            <h1>Contacts</h1>
            <input className="input2" placeholder="Search"
            onChange={handleSearch}
            ></input>
        </div>
        <div className="listItems">

            {!search?contacts.map((C)=>{
                return(
                    <ContactListItem
                                key={C.username}
                                contact={C}
                                onClick={()=>{
                                    prop.setSettings(false);
                                    prop.selectContact(C);
                                }}/>
                );
            }):search.map((C)=>{
                return(
                    <ContactListItem 
                                contact={C}
                                onClick={(e)=>{
                                                handleAddContact(C.username);
                                                prop.selectContact(C);
                                                }}
                                />
                )
            })}

        </div>
        </div>
    );
}

const ContactListItem=(props)=>{
    return(
        <div className="ContactListItem"
                onClick={props.onClick}
                >
            <img className="profile1x1" src={props.contact.profilePic} draggable='false'/>
            <div className="vert1">
            <h3>{props.contact.displayname}</h3>
            <span className="un">@{props.contact.username}</span>
            </div>
        </div>
    );
}

export default ContactList;