import React, { useEffect, useRef, useState } from "react";
import {io} from 'socket.io-client';
var socket=io({
    query:{token:document.cookie.split('=')[1]},
});


const Chat=(props)=>{
    const [data,setData]=useState([]);
    const [inputData,setInputData]=useState('');
    const [fileIn,setFileIn]=useState(false);
    const [file,setFile]=useState(false);
    
    const inputField=useRef(null);
    const messages=useRef(null);
    const bottomRef=useRef(null);

    
    useEffect(()=>{
        if(props.contact){
            let xhr=new XMLHttpRequest();
            xhr.open('POST',`/getMessages`);
            xhr.setRequestHeader('Content-Type','application/json');
            xhr.send(JSON.stringify({
                contact:props.contact.username
            }));
            xhr.onload=()=>{
                setData(JSON.parse(xhr.responseText));
            }
        }
    },[props.contact]);
    useEffect(()=>{
        document.addEventListener('keydown',(e)=>{
            if(e.key==='Escape'){
                setFileIn(false);
                setFile(null);
            }
        });
    },[]);
    useEffect(()=>{
        if(bottomRef.current){bottomRef.current.scrollIntoView({behavior:'smooth'});}
    },[data]);
    
    useEffect(()=>{
            socket.emit('setOnline');
    },[props.user]);
    

    const send=async ()=>{
        if(file){
            let m={
                to:props.contact.username,
                from:props.user.username,
                type:'image',
                data:file,
                text:inputData,
            }
            await socket.emit('PrivateMessage',m);
            setInputData('');
            inputField.current.value='';
            let d=data.concat(m);
            setData(d);
            setFileIn(false);
            setFile(false);
        }else if(inputData.length>0 && props.contact){
            let m={
                to:props.contact.username,
                from:props.user.username,
                type:'text',
                data:inputData,
            }
            await socket.emit('PrivateMessage',m);
            setInputData('');
            inputField.current.value='';
            let d=data.concat(m);
            setData(d);
        }
    }
    
    socket.on('incomingMessage',(dt)=>{
        let m={
            type:dt.type,
            data:dt.data,
            from:dt.from,
        }
        console.log('bzz');
        if(dt.from===props.contact.username){
            let d=data.concat(m);
            setData(d);
        }
    });

    const handleDrop=(event)=>{
        setFile(false);
        event.preventDefault();
        if(event.dataTransfer.files[0].type.substring(0,5)=='image' && event.dataTransfer.files[0].size<15000000){
            let reader=new FileReader();
            reader.onloadend=()=>{
                setFile(reader.result);
            }
            reader.readAsDataURL(event.dataTransfer.files[0]);
        }
    };

    const handleBrowse=()=>{
        let input = document.createElement('input');
        input.type='file';
        input.onchange=()=>{
            if(input.files[0].type.substring(0,5)=='image' && input.files[0].size<15000000){
                let reader=new FileReader();
                reader.onloadend=()=>{
                    setFile(reader.result);
                }
                reader.readAsDataURL(input.files[0]);
            }
        }
        input.click();
    }
    return(
        <div className="chatContainer">
        {props.contact?<>  
        <div className="nav2">
            <img className="profile1x1" src={props.contact.profilePic}></img>
            <h2>{props.contact.displayname}</h2>
        </div>
        <div ref={messages} className="messages">
            {
                data.map(m=>{
                    return(
                        <Message
                            type={m.type}
                            user={m.from==props.user.username?'send':'recieved'}
                            data={m.data} text={m.text?m.text:''}
                            previewImage={props.previewImage}
                            />
                    )
                })
            }
            <span ref={bottomRef}></span>
        </div>
        <div className="chatInput">
            <input 
                ref={inputField}
                className="input3"
                onInput={(e)=>{setInputData(e.target.value)}}
                onKeyDown={(e)=>{if(e.key==='Enter'){send();}}}
                />
            <div className="btn2 bc1"  onClick={()=>{send();}}>&#10148;</div>
            <div className="btn2 bc2" onClick={()=>{
                                                    setFile(null);
                                                    fileIn?setFileIn(false):setFileIn(true)
                                                    }}
                                                    >&#10064;</div>
        </div>
        {fileIn?<FileInput handleBrowse={handleBrowse} handleDrop={handleDrop} file={file}/>:''}
        </>:<>
        <img className="image1" src="./img/blank.jpg"/>
        </>}
        </div>
    );
}
export default Chat;

const Message=(props)=>{
    if(props.type=='text'){
        let cls='message '+props.user;
        return(
            <div className={cls}>
                {props.data}
            </div>
    );
    }else if(props.type=='image'){
        let cls='message '+props.user;
        return(
            <div className={cls}>
                <img src={props.data}
                onClick={()=>{
                    props.previewImage(props.data);
                }}                
                className='image1x1'/>
                {props.text}
            </div>
            )
    }
}
const FileInput=(props)=>{
    return(
        <div className="fileInput" 
                onDragEnter={(event)=>{event.preventDefault()}}
                onDragOver={(event)=>{event.preventDefault()}}
                onDrop={(event)=>{props.handleDrop(event)}}
        >
            {props.file?
            
            <img src={props.file} className='imagePreview1'/>
            :<>
            <img src="./img/drop.png" className="symbol"/>
            <h2>Drag Drop File Here</h2>
            <div className="btn1 bc1" onClick={props.handleBrowse}>Browse File</div>
            </>
            }
        </div>
    );
}
