import { useEffect } from "react";

 const ImagePreviw=(props)=>{
    useEffect(()=>{
        document.addEventListener('keydown',(e)=>{
            if(e.key==='Escape'){
                props.endPreview();
            }
        });
    },[]);
    return(
        <div className="imagePreview2">
            <div className="btn2" onClick={props.endPreview}>&#9932;</div>
            <img src={props.image} alt="<h2>Something Went Wrong !</h2>"/>
        </div>
    )
}

export default ImagePreviw;