import { useState ,useEffect } from "react";
import Login from "./Login.js";
import ContactList from './ContactList';
import Chat from './Chat';
import { loadUserData } from "./loadData.js";
import ImagePreviw from './imagePreview.js';
import ProfilSettings from "./profileSettings.js";
const Container=()=>{
    let [user,setUser]=useState(false);
    let [selected,setSelected]=useState(null);
    let [preview,setpreview]=useState(null);
    let [settings,setSettings]=useState(false);
    
    const logout=()=>{
        setUser(false);
        document.cookie='token=';
    }
    const onLogin=(username)=>{
        loadUserData(username,setUser);
    }
    useEffect(()=>{
        const xhr = new XMLHttpRequest();
        xhr.open('POST','/authToken');
        xhr.send();
        xhr.onload=()=>{
            let res=JSON.parse(xhr.responseText);
            if(res.validity){
                onLogin(res.username);
            }
        }
    },[]);
const previewImage=(img)=>{
    setpreview(img);
}
const endPreview=()=>{
    setpreview(null);
}
const toggleSettings=()=>{
    settings?setSettings(false):setSettings(true);
}
    return(
        <>
        {preview?<ImagePreviw endPreview={endPreview} image={preview}/>:''}
        <div className="container">
            {!user?<Login onLogin={onLogin}/>:<></>}          
            {user?      
            <div className="sidePanel">
                <div className="userInfo">
                    <img className="profile1x1"
                     src={user.profilePic}
                     onClick={toggleSettings}
                     />
                    <h2>{user.name}</h2>
                    <div className="btn1"
                        onClick={()=>{logout()}}
                    >Logout</div>
                </div>
            <ContactList setSettings={setSettings} selectContact={setSelected}/>
            </div>:<></>}
            {user && !settings?<Chat endPreview previewImage={previewImage} contact={selected} user={user}/>:<></>}
            {settings && user?<ProfilSettings reload={onLogin} user={user}/>:<></>}        
        </div>
        </>
    );
}
export default Container;
